










import { useCool } from "/@/cool";

const { service, router, ui } = useCool();

// 搜索
function onSearch(val: string) {
	if (val) {
		service.chat.friend
			.search({ num: val })
			.then((res) => {
				if (res) {
					router.push({
						path: "/pages/chat/user-detail",
						query: {
							id: res.id,
						},
						mode: "redirectTo",
					});
				} else {
					ui.showTips("该用户不存在");
				}
			})
			.catch((err) => {
				ui.showTips(err.message);
			});
	}
}
